import React from 'react'
import "../App.css";
import MenuIcon from '@mui/icons-material/Menu';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { useParams } from "react-router-dom";

function Menu(props){
  let { username } = useParams();
  return (
    <div className="main-container">
        <div className="menu-container">
        <div className="menu-nav">
            <MenuIcon/>
            <div>{props.companyName}'s Menu</div>
            <ShoppingCartIcon/>
        </div>
        <div className="info-div">
            <h1>{props.menu} Menu</h1>
            <p>Select your category below.</p>
        </div>
        <div className="menu-items">Starters</div>
        <div className="menu-items">FATHER'S DAY SPECIALS</div>
        <div className="menu-items">BREADS</div>
        <div className="menu-items">Beverages</div>
        <div className="menu-items">Food</div>
    </div>
    </div>
    
  )
}

export default Menu;