import React, { useState } from "react";
import VisibilityIcon from "@mui/icons-material/Visibility";
import "../App.css";
import axios from 'axios'
import { useNavigate, useParams } from "react-router-dom";

function SignIn() {
  const navigate = useNavigate()
  const [id,setId] = useState([])
  const [password,setPassword] = useState([])

  function handleSubmit(event){
  event.preventDefault();

  axios.post('http://localhost:8081/signin',{id,password})
  .then(response => {
    if (response.data === 'No'|| id.length === 0 || password.length === 0 ){
      alert('Data not Found !')
    }else{
      console.log(response)
      navigate(`/menu/${response.data}`)
  }
}
)
  .catch(error => console.log(error))

  }

 

  return (
    <div className="signin">
      <a href="/" className="signin-first">
        <h3>Dining-Dots</h3>
        <h1>Partner Dashboard</h1>
      </a>
      <div className="signin-second">
        <div className="signin-title">
          <h1>Sign-In</h1>
        </div>
        <div className="form-div">
          <form onSubmit={handleSubmit}>
          <h2>Partner ID</h2>
          <input placeholder='12345' type="id" onChange={a => {setId(a.target.value)}}/>
          <h2>Password</h2>
          <div className="input-div">
            <input placeholder='pass123' type="password" onChange={b => {setPassword(b.target.value)}}/>
            <div id="show-icon">
            <VisibilityIcon />
            </div>
         
          </div>
          <button type="submit" className="button">
            Sign In
          </button>
          </form>
          
        </div>
        <div></div>
        <div>
          <p></p></div>
      </div>
    </div>
  );
}

export default SignIn;
