import React from "react";
import "../App.css";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import InstagramIcon from "@mui/icons-material/Instagram";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import QRCode from "react-qr-code";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
function MenuPage() {
  let { username } = useParams();
 

  return (
    <div className="main-container">
      
      <div className="menu-container">
        <div className="menu-title">
          <h2>Welcome to {username}</h2>
        </div>
        <div className="menu-icons">
          <LocationOnIcon />
          <InstagramIcon />
          <WhatsAppIcon />
        </div>
        <div className="menuoptions">
          <Link  to={'Dine-in'} className="menuoptions-div">
            <h2>Dine-in Menu</h2>
          </Link>
          <Link to={'Delivery'} className="menuoptions-div">
            <h2>Delivery Menu</h2>
          </Link>
          <div className="menuoptions-div">
            <InstagramIcon />
            <h2>FOLLOW US ON INSTAGRAM!</h2>
          </div>
          <div className="qrcode-div" id="qrCode">
          <h2>Your QR Code</h2>
          <Link to="Dine-in">
          <QRCode value={'http://192.168.29.121:3000/menu/Dine-in'} />
          </Link>
          
        </div>
        </div>
        
      </div>
    </div>
  );
}

export default MenuPage;
