import React from "react";

function PriceCard(props){
    let numberofService = props.serviceNumber;

    const services = [
        'Free QR code',
        'Unlimited menu items & photos',
        'Menu Management',
        'Analytics',
        'WhatsApp and SMS Marketing tools',
        'Instagram account integration',
        'WhatsApp order notifications',
        'Free Loyalty program',
        'Online Ordering',
        'Payment Gateway Integration',
        'Custom Domain',
        'POS Integrations',
        'Complete Branding Control',
    ];
    function displayServicesProvided(){
        let index = 0
        let servicesProvided = [];
        for (let _ in services){
            if (index < numberofService){
                servicesProvided.push(
                    <div className="bold">✔ {services[index]}</div>
                );
                index ++
            }else{
                servicesProvided.push(
                    <div className="light">{services[index]}</div>
                );
                index ++
            }
        }
        return servicesProvided;
        
    };

    return (
        <div className="cardCont" >
            <div className={`titleDiv backG-${props.color}`}>
                <h2>{props.title}</h2>
            </div>
            <div className="priceCont">
                <h1>Rs {props.price} /Month</h1>
            </div>
            <p>Billed annually</p>
            <div className="desc">
                {props.desc}
            </div>
            <div className="services">
                {displayServicesProvided()}
            </div>
            <div className={`getintouch backG-${props.color}`}>
            Get in touch
            </div>

        </div>
        )
}

export default PriceCard;