import React from "react";
import Navbar from "./navbar";
import Footer from "./footer";
import PriceCard from "./priceCard";
import "../App.css";

function PricePage() {
  return (
    <div>
      <Navbar />
      <div className="pricingCont">
        <h1>Simple and fair pricing.</h1>
        <h3>Our Pricing Plans</h3>
      </div>
      <div className="cards">
        <PriceCard
          title="Pro"
          price="799"
          desc="Transition from the traditional paper menus to QR code enabled beautiful mobile menu."
          serviceNumber="6"
          color='green'
        />
        <PriceCard
          title="Advanced"
          price="959"
          desc="Deliver directly to your customers at 0% commissions. Get your online ordering website integrated with Dining-Dots logistics partners."
          serviceNumber="9"
          color='orange'
        />
        <PriceCard
          title="Premium"
          price="1499"
          desc="For brands that want it all! Get your personalised branding and all features Dining-Dots has to offer."
          serviceNumber="13"
          color='red'

        />
      </div>

      <Footer />
    </div>
  );
}
export default PricePage;
