import React from "react";
import background from "../assests/bg.png";
import "../App.css";
import VerifiedIcon from '@mui/icons-material/Verified';

function Container() {
  return (
    <div className="info-container">
      <div className="container-info">
        <div className="infodiv">
          <div className="headline">
            <h1>Create your Beautiful mobile menu with Dining Dots</h1>
          </div>
          <div className="headline-info">
            Restaurants | Hotels | Chefs | Bakeries | Spa | Super markets
          </div>
          <div className="info-buttons">
            <a href="#">
              <div className="bookdemo">
                <h2>Book A Demo</h2>
              </div>
            </a>
            <a href="#">
              <div className="free-trail">
                <h2>Start Free Trail</h2>
              </div>
            </a>
          </div>
          <div className="trusted" >
            <VerifiedIcon/>
             <h4>TRUSTED BY 1500+ BRANDS</h4>
          </div>
        </div>
      </div>
      <div className="container-image">
        <img src={background} alt="bg" />
      </div>
    </div>
  );
}

export default Container;
